.unclicked_button{
    width:50%;
   
    font-size: large;
    border :1px solid #163948;
	background-color: white;
	padding:10px 40px;
	margin:5%;
	padding-left: 15%;
	padding-right: 15%;
    border-radius: 5px;
    margin-top: 10%;
}
.unclicked_button:hover {
	background:linear-gradient(to bottom, #BCD8DF 5%);
	background-color:#BCD8DF ;
}

.clicked_button{
    width:50%;
   
    font-size: large;
    border :1px solid #163948;
	background-color: #BCD8DF;
	padding:10px 40px;
	margin:5%;
	padding-left: 15%;
	padding-right: 15%;
    border-radius: 5px;
    margin-top: 10%;
    
}

